<style  scoped>
h2 {
  padding: 5px;
  background: rgba(0, 0, 0, 0.06);
  color: rgba(0, 0, 0, 1);
}
</style>
<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="6">
        <v-layout wrap>
          <v-col cols="12" sm="12">
            <h2>Stock Prices</h2>
            <v-text-field
              hint="Enter Numbers Separated by Space or Comma(,). Ex-1: 300, 900 or Ex-2:300 900"
              label="Number of Stocks"
              clearable
              filled
              v-model="stocks"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <h2>Number of Stocks</h2>
            <v-text-field
              hint="Enter Price Separated by Space or Comma(,). Ex-1: 200, 450 or Ex-2:200 450"
              label="Respective Price"
              clearable
              filled
              v-model="prices"
            ></v-text-field>
          </v-col>
        </v-layout>
      </v-col>
      <v-col cols="12" sm="6">
        <v-layout>
          <v-flex md12>
            <v-card elevation="12" color="#607d8b" style="">
              <v-card-title>
                <v-flex md5>
                  <strong style="padding-right: 5px; color: white"
                    >Total Price
                  </strong></v-flex
                >
                <strong style="color: white; padding-left: 5px">{{
                  totalPrice
                }}</strong></v-card-title
              >
              <v-card-title>
                <v-flex md5>
                  <strong style="padding-right: 5px; color: white"
                    >Total Number of Stocks
                  </strong></v-flex
                >
                <strong style="color: white; padding-left: 5px">{{
                  totalStockNumber
                }}</strong></v-card-title
              >
              <v-card-title
                ><v-flex md5>
                  <strong style="padding-right: 5px; color: white"
                    >Average Price Per Stock
                  </strong>
                </v-flex>

                <strong style="color: white; padding-left: 5px">{{
                  stocksSize
                }}</strong></v-card-title
              >
              <v-card-title
                ><v-flex md5>
                  <strong style="padding-right: 5px; color: white"
                    >Total Ammount Spent
                  </strong>
                </v-flex>

                <strong style="color: white; padding-left: 5px">{{
                  totalAmount
                }}</strong></v-card-title
              >
            </v-card>
          </v-flex>
        </v-layout>
      </v-col>
    </v-row>
    <br />
  </v-container>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "Home",
  data: () => ({
    stocks: "960 880 850",
    prices: "10 10 10",
    totalAmount: "",
  }),

  computed: {
    stocksSize() {
      return this.stocks.length > 0 && this.prices.length > 0
        ? String(this.stocks).match(",") != null
          ? this.stocks.trim().split(",").length ==
            this.prices.trim().split(",").length
            ? this.calcAvg(
                this.stocks.trim().split(","),
                this.prices.trim().split(",")
              )
            : "Enter Respective price"
          : this.stocks.trim().split(",").length ==
            this.prices.trim().split(",").length
          ? this.calcAvg(
              this.stocks.trim().split(" "),
              this.prices.trim().split(" ")
            )
          : "Enter Respective price"
        : "Enter Number";
    },
    totalStockNumber() {
      return this.stocks.length > 0
        ? String(this.stocks).match(",") != null
          ? this.stocks
              .trim()
              .split(",")
              .reduce((total, arg) => total + parseInt(arg), 0)
          : this.stocks
              .trim()
              .split(" ")
              .reduce((total, arg) => total + parseInt(arg), 0)
        : "Enter a Number";
    },
    totalPrice() {
      return this.prices.length > 0
        ? String(this.prices).match(",") != null
          ? this.prices
              .trim()
              .split(",")
              .reduce((total, arg) => total + parseInt(arg), 0)
          : this.prices
              .trim()
              .split(" ")
              .reduce((total, arg) => total + parseInt(arg), 0)
        : "Enter a Number";
    },
  },
  methods: {
    calcAvg(arr1, arr2) {
      let avg = 0;
      let total = arr2.reduce((total, arg) => total + parseInt(arg), 0);
      if (arr1.length == arr2.length) {
        arr1.forEach((elem, i) => {
          avg += parseInt(arr1[i]) * parseInt(arr2[i]);
        });
        this.totalAmount = avg;
      }
      return avg / total;
    },
  },
};
</script>
